'use strict';

import "./styles.css";
import { html, render, PrintPreview } from './UIComponents';
import { printScreenShot, getScreenShot } from './PrintHelper';
import { locales } from './Locale';

const av = Autodesk.Viewing;

export class VaultPrintExtension extends av.Extension {
    
    constructor(viewer, options) {
        super(viewer, options);
        this.modes = [];
        this.mode = '';
        this.name = 'VaultPrint';
    }

    load() {
        super.load();
        this.extendLocalization(locales);
        return true;
    }

    activate(mode) {
        super.activate(mode);
        return true;
    }

    async onToolbarCreated(toolbar) {
        super.onToolbarCreated(toolbar);

        const printBtn = new Autodesk.Viewing.UI.Button("toolbar-print");
        printBtn.setToolTip("Print Snapshot");
        printBtn.setIcon("v-icon-print-snapshot");
        printBtn.onClick = this.handlePrintClick.bind(this);

        this.printToolbar = new Autodesk.Viewing.UI.ControlGroup('print-toolbar');
        this.printToolbar.addControl(printBtn);
        toolbar.addControl(this.printToolbar);
    }

    async showPreview({ blobUrl, width, height }){

        const doc = this.getDocument();
        const uiContainer = doc.createElement('div');
        uiContainer.className = 'print-priview-container';
        this.viewer.container.appendChild(uiContainer);

        const props = {
            blobUrl,
            onCancel: () => {
                render(null, uiContainer);
                uiContainer.remove();
                this.getWindow().URL.revokeObjectURL(blobUrl);
            },
            onPrint: async () => {
                render(null, uiContainer);
                uiContainer.remove();
                await printScreenShot(this.getWindow(), blobUrl, width, height);
            }
        };
        render(html`<${PrintPreview} ...${props} />`, uiContainer);
    }

    async handlePrintClick(){
        const { preview } = this.options;
        this.viewer._loadingSpinner.show();
        const { blobUrl, width, height } = await getScreenShot(this.viewer);
        if(preview){
            this.showPreview({ blobUrl, width, height });
        }else{
            await printScreenShot(this.getWindow(), blobUrl, width, height);
        }
        this.viewer._loadingSpinner.hide();
    }

    unload() {
        super.unload();

        if (this.printToolbar) {
            this.viewer.toolbar.removeControl(this.printToolbar);
            this.printToolbar = null;
        }

        return true;
    }

    deactivate() {
        super.deactivate();
        return true;
    }
}

av.theExtensionManager.registerExtension('Autodesk.Vault.Print', VaultPrintExtension);
