'use strict';

const av = Autodesk.Viewing;
const PAGE_SIZES = {
    A0: { width: 7022, height: 4967 },
    A1: { width: 4967, height: 3508 },
    A2: { width: 3508, height: 2480 },
    A3: { width: 2480, height: 1754 },
    A4: { width: 1754, height: 1240 },
};

const calculatePageSize = (viewer, highRes = true) => {

    const clientRect = viewer.impl.getCanvasBoundingClientRect();
    const canvasWidth = clientRect.width;
    const canvasHeight = clientRect.height;
    const canvasRatio = canvasHeight / canvasWidth;
    const isLandscape = canvasRatio < 1.0;
    let pageWidthInPixels = av.isIE11 ? PAGE_SIZES.A4.width : PAGE_SIZES.A0.width;
    const is2D = viewer.impl.is2d;

    if (is2D) {
      const pageWidth = viewer.model.getMetadata('page_dimensions', 'page_width');
      const pageUnits = viewer.model.getMetadata('page_dimensions', 'page_units');

      if (pageUnits) {
        const pageWidthInInch = av.Private.convertUnits(pageUnits.toLowerCase(), 'in', 1, pageWidth);
        const DPI = 150;
        pageWidthInPixels = Math.ceil(pageWidthInInch * DPI);
      } else {
        // If there is no page_units in the metadata, the document is a raster image - use the dimensions as pixels.
        pageWidthInPixels = pageWidth;
      }
    }

    const outputWidth = highRes ? pageWidthInPixels : PAGE_SIZES.A2.width;
    const is2dOrLandscape = is2D || isLandscape;
    const width = Math.ceil(is2dOrLandscape ? outputWidth : outputWidth / canvasRatio);
    const height = Math.ceil(is2dOrLandscape ? outputWidth * canvasRatio : outputWidth);

    return { width, height };
};

const getScreenShot = (viewer, highRes = true) => {
    return new Promise((resolve) => {
        
        const is2D = viewer.impl.is2d;
        const { width, height } = calculatePageSize(viewer, highRes);
        
        // full page for 2D?
        const options = { fullPage: is2D, margin: 0 };
        av.ScreenShot.getScreenShotWithBounds(viewer, width, height, (blobUrl, width, height) => {
            resolve({blobUrl, width, height});
        },options);        
    });
};
const generateFrameHTML = ({ blobUrl, width, height }) => {
    return `<!DOCTYPE html>
        <html lang="en">
            <head>
                <style> 
                    @media print {
                        html, body {
                            margin: 0;
                            padding: 0;
                            width: 100%;
                            height: 100%;
                        }
                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: contain;
                            margin-top: -4px;
                            margin-left: -16px;
                        }
                    } 
                </style>
                <script>
                    window.addEventListener("afterprint", function(e) {
                        if(window.parent){
                            window.parent.postMessage('print success', window.parent.location.origin);
                        }
                    });
                    function tryPrint(){
                        window.print();
                    }
                </script>
            </head>
            <body>
                <img src="${blobUrl}" onload="tryPrint()" />
            </body>
        </html>`;
};
const printScreenShot = (global, blobUrl, width, height) => {
    return new Promise((resolve) => {
        const frameHtml = generateFrameHTML({blobUrl, width, height});
        const frameBlobUrl = global.URL.createObjectURL(new Blob([frameHtml],{ type: 'text/html'}));
        const printFrame = global.document.createElement('iframe');
        printFrame.name = 'printFrame';
        printFrame.src = frameBlobUrl;
        printFrame.width=0;
        printFrame.height=0;
        printFrame.style.visibility = 'hidden';

        const onPrintSuccess = (e) => {
            if (e.source === printFrame.contentWindow && e.data === 'print success'){
                global.removeEventListener("message", onPrintSuccess);
                printFrame.remove();
                global.URL.revokeObjectURL(frameBlobUrl);
                global.URL.revokeObjectURL(blobUrl);
                resolve();
            }
        };
        global.document.body.appendChild(printFrame);
        global.addEventListener("message", onPrintSuccess, false);
        });
};

export {
    calculatePageSize,
    getScreenShot,
    printScreenShot,
};